@import "../global.scss";

.modules {
  justify-content: center;
  display: grid;
  height: 100vh;
  grid-template-rows: 1fr 1fr;

  a {
    &:nth-child(2) {
      background: $darkBlue;
    }
    margin: 0 auto;
    height: 30rem;
    width: 70rem;
    border: solid 0.1rem $mainBlue;
    display: grid;
    padding: 3rem;
    &:hover {
      background: $lightBlue;
    }
  }

  .lock {
    padding: 2rem;
    width: 10rem;
    position: absolute;
  }

  .modulesIdentifier {
    color: $mainBlue;
    font-size: 2.5rem;
    font-weight: 100;
    margin-bottom: 3rem;
  }
}

.module {
  display: flex;

  @include device("MD") {
    width: 60rem;
  }

  .icon {
    width: 20rem;
  }
  .name {
    color: $mainBlue;
    font-weight: 700;
    font-size: 4rem;
    align-self: center;
    text-align: center;
    width: 50%;
  }
}
