@import "../global.scss";

.form {
  display: grid;
  padding: 4rem;
  .logo {
    justify-self: center;
    width: 10rem;
  }
  .title,
  .subtitle {
    justify-self: center;
  }
  .title {
    color: $darkRed;
    font-size: 4rem;
    font-family: $heavyFont;
  }
  .subtitle {
    color: $darkRed;
    font-weight: 600;
    font-size: 2.5rem;

    b {
      font-weight: 900;
    }
  }

  &Content {
    display: grid;
    margin-top: 7rem;
    justify-items: center;

    label {
      color: $darkBlue;
      font-size: 4rem;
      margin-bottom: 3rem;
    }

    input {
      width: 45rem;
      border-radius: 3rem;
      height: 6rem;
      border: $darkBlue 0.2rem solid;
      color: $darkBlue;
      text-align: center;
      font-size: 4rem;
    }

    .submit {
      display: block;
      margin-top: 3rem;
      width: 35rem;
      padding: 0.5rem 4rem;
      font-size: 5rem;
      font-weight: 700;
      background: $darkBlue;
      color: $light;
      border: $light 0.2rem solid;
    }

    .formBack {
      padding: 1rem;
      margin-top: 5rem;
      background: $lightBlue;
      border: solid 0.2rem $darkBlue;

      img {
        width: 5rem;
      }
    }

    .reset {
      display: flex;
      align-items: center;
      background: none;
      border: none;
      margin-top: 4rem;
      padding: 1rem;
      color: $darkRed;
      img {
        width: 4rem;
        margin-right: 1rem;
      }
    }
  }
}
