@import "../global.scss";

.pageButtons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 5rem;

  .nextPage {
    justify-self: end;
  }

  .return {
    background: $darkPurple;
    color: #c6ffa0;
    font-size: 2rem;
    border-radius: 5rem;
    border: $mainBlue solid 0.3rem;
    width: 12rem;
    display: block;
    height: 100%;
  }

  button {
    &:hover {
      transform: scale(1.1);
      cursor: pointer;
    }
    padding: 1rem;
    background: none;
    border-radius: 50%;
    height: 8rem;
    width: 8rem;
    border: solid 0.1rem $lightRed;
    display: flex;

    img {
      margin: 0 auto;
    }
  }
}

.backModules {
  background: $darkPurple;
  color: $lightBlue;
  padding: 2rem;
  border: 0.2rem solid $dark;
  margin: 1rem auto;
  display: block;
}
.pages {
  width: 65%;
  margin: 10rem auto;

  @include device("XLG") {
    width: 80%;
  }

  @include device("LG") {
    width: 80%;
  }

  @include device("MD") {
    width: 70%;
  }

  @include device("SM") {
    width: 95%;
  }

  b {
    color: $mainBlue;
  }

  .page1 {
    display: flex;

    img {
      width: 45rem;
      display: block;
      margin: 0 auto;
    }
    p {
      margin-top: 3rem;
      font-size: 3rem;
    }
  }

  .page2 {
    display: flex;
    .photo {
      display: block;
      margin: 0 auto;
      width: 25rem;
    }
    .arrows {
      display: block;
      margin: 0 auto;
      width: 65rem;
    }
    .heading {
      font-size: 3.5rem;
      font-weight: 500;
      text-align: center;
    }

    hr {
      border: 0.1rem solid $lightRed;
      margin: 3rem;
    }

    .text {
      font-size: 2.5rem;
    }
  }

  .page3 {
    display: grid;

    .instructions {
      display: block;
      margin: 0 auto;
      width: 80%;
      font-size: 3.5rem;
      font-weight: 100;
    }

    .completedText {
      color: $darkBlue;
      font-size: 3rem;
      border: solid 1rem $darkBlue;
      padding: 3rem;
      margin-top: 5rem;
      text-align: center;
    }

    .knowledgeCard {
      margin: 0 auto;
      background: $darkBlue;
      height: 50rem;
      width: 50rem;
      padding: 5rem 2rem;
      display: grid;
      margin-top: 10rem;
      border-radius: 4rem;

      .statement {
        align-self: end;
        text-align: center;
        color: $light;
        font-size: 2.5rem;
        font-weight: 900;
      }
      .arrows {
        height: 12rem;
        border-radius: 10rem;
        align-content: center;
        align-self: end;
        width: 95%;
        display: grid;
        grid-template-columns: auto auto;
        margin: 0 auto;
        justify-content: center;
        grid-column-gap: 20rem;
        padding: 2rem;
        background: white;
        img {
          width: 8rem;
        }
      }
    }
  }

  .page4 {
    justify-content: center;

    .submit {
      color: $lightBlue;
      background: $darkBlue;
      padding: 1rem 3rem;
      display: block;
      margin: 5rem auto;
      font-size: 1.5rem;
      border: solid 0.5rem $darkPurple;
    }

    .text {
      padding: 2rem;
      font-size: 3rem;
      color: $darkBlue;
    }
    label {
      font-size: 3rem;
      display: flex;
      justify-content: center;
      margin-top: 5rem;
      span {
        color: $darkBlue;
        margin-right: 5rem;
      }
    }
    input {
      border: solid $mainBlue 0.5rem;
      width: 10rem;
      padding: 1rem;
      text-align: center;
      color: $mainBlue;
    }
  }

  .page5 {
    font-size: 3.5rem;
    justify-content: center;
  }

  .page6 {
    justify-content: center;

    img {
      margin: 5rem 0 5rem 0;
      width: 100%;
    }

    p {
      font-size: 2.5rem;
    }
    justify-content: center;
  }

  .page7 {
    justify-content: center;
    hr {
      border: solid 0.1rem $darkBlue;

      justify-self: center;
      margin-top: 3rem;
    }
    h2 {
      color: $mainBlue;
      font-size: 2rem;
    }

    img {
      margin: 5rem 0 5rem 0;
      width: 100%;
    }

    p {
      font-size: 2.5rem;
    }
  }
}

.page8 {
  justify-content: center;
  .problem {
    margin-bottom: 4rem;
    background: $darkBlue;
    color: $light;
    padding: 5rem;
    font-size: 3.5rem;
  }

  .options {
    margin: 3rem;
    button {
      background: $lightBlue;
      border: 0.5rem solid $darkPurple;
      padding: 1rem 2rem;
      border-radius: 3rem;
      color: $darkPurple;
      margin: 2rem;
    }
  }

  input {
    justify-self: center;
    margin: 3rem auto;
    display: block;
    text-align: center;
    border: 0.5rem solid $mainBlue;
    color: $mainBlue;
    width: 15rem;
  }

  .submit {
    display: block;
    margin: 3rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    background: $darkPurple;
    color: $lightBlue;
    border: $darkBlue 0.4rem solid;
    font-size: 2rem;
  }

  p {
    font-size: 2.5rem;
  }
}

.page9 {
  p {
    font-size: 3rem;
  }

  form {
    font-size: 3rem;

    input {
      width: 10rem;

      margin: 3rem auto;
      text-align: center;
      border: 0.5rem solid $mainBlue;
    }
    .submit {
      display: block;
      margin: 3rem;
      margin: 0 auto;
      padding: 1rem 2rem;
      background: $darkPurple;
      color: $lightBlue;
      border: $darkBlue 0.4rem solid;
      font-size: 2rem;
    }
  }
}

.page10 {
  font-size: 3rem;
}

.modulesHeader {
  display: flex;
  grid-template-rows: auto;
  grid-template-columns: auto;
  background: $darkPurple;
  width: 85%;
  margin-left: 15%;

  .lessonNumber {
    display: grid;
    position: relative;
    left: 6rem;
    bottom: 1.5rem;
    img {
      width: 6rem;
      position: relative;
      right: 9rem;
    }
    .number {
      position: relative;
      right: 6.5rem;
      bottom: 4.5rem;
      color: $lightBlue;
      font-size: 3rem;
    }
  }

  .name {
    text-align: center;
    align-self: center;
    font-size: 4rem;
    color: $light;
  }
}
