@import url("https://use.typekit.net/mlu1dcr.css");

$darkBlue: #4156aa;
$mainBlue: #636fea;
$darkPurple: #6c08c4;
$darkRed: #9a4848;
$light: #e5eaff;
$dark: #010410;
$lightBlue: #c3defb;
$lightRed: #fbd6d6;

$mainFont: quasimoda, sans-serif;
$heavyFont: quasimoda-2, sans-serif;

@mixin device($breakpoint) {
  @if $breakpoint == "XSM" {
    @media (max-width: 25.875em) {
      @content;
    }
  }

  @if $breakpoint == "SM" {
    @media (max-width: 48em) {
      @content;
    }
  }

  @if $breakpoint == "MD" {
    @media (max-width: 64em) {
      @content;
    }
  }

  @if $breakpoint == "LG" {
    @media (max-width: 85.375em) {
      @content;
    }
  }

  @if $breakpoint == "XLG" {
    @media (min-width: 120em) {
      @content;
    }
  }
}

html {
  font-size: 62.5%;

  @include device("XLG") {
    font-size: 75%;
  }

  @include device("LG") {
    font-size: 56%;
  }

  @include device("MD") {
    font-size: 50%;
  }

  @include device("SM") {
    font-size: 35%;
  }

  @include device("XSM") {
    font-size: 30%;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: $mainFont;
}

a {
  text-decoration: none;
}

button:hover {
  transform: scale(1.1);
  cursor: pointer;
  transition: transform 0.2s;
}

h1,
h2,
h3,
p,
span {
  margin: 0;
  padding: 0;
}
