@import "../global.scss";

.user {
  display: grid;
  height: 100vh;

  .account {
    display: block;
    justify-self: end;
    width: 9rem;
    padding: 1rem;
    margin: 1rem;
    margin-bottom: 5rem;
    box-shadow: 0px 3px 4px 0px $lightRed;
    &:hover {
      transform: scale(1.1);
      cursor: pointer;
      background: $darkPurple;
    }
  }

  .topMenu {
    display: grid;
    width: 100%;
  }
  .modalLogout {
    .close {
      color: $lightRed;
      background: $darkRed;
      font-size: 2rem;
      padding: 1rem;
      padding: 0 1rem;
      border: 0.2rem $dark solid;
    }
    justify-self: end;
    position: absolute;
    right: 1rem;
    background: $mainBlue;

    top: 10rem;
    font-size: 1.6rem;
    a {
      padding: 2rem;
      color: $lightBlue;
    }
  }

  .bottomMenu {
    background: lighten($lightRed, 7%);
    width: 100%;
    height: 12vh;
    padding: 2rem;
    position: sticky;
    bottom: 0;
    display: flex;

    .icons {
      display: grid;
      width: 90%;
      grid-template-columns: 15rem 15rem 15rem;
      align-content: center;

      img {
        box-shadow: 0px 3px 4px 0px $lightRed;
        width: 7rem;
        padding: 1.2rem;
        margin-right: 4rem;
        &:hover {
          transform: scale(1.1);
          cursor: pointer;
          background: $darkPurple;
        }
      }
    }
    .progress {
      align-self: center;
      width: 9rem;
      border: solid $lightRed 1rem;
      border-radius: 50%;
      height: 9rem;
      display: flex;
      justify-content: center;
      align-items: center;

      &Percentage {
        color: $darkBlue;
        font-weight: 900;
        font-size: 2rem;
        text-align: center;
      }
    }
  }
}
