@import "../global.scss";

.logo {
  height: 20vh;
  margin: 0 auto;
  display: block;
}

.start {
  display: grid;
  justify-content: center;
  justify-items: center;
  padding: 8rem;

  background: linear-gradient(180deg, $darkRed 0%, $darkBlue 100%);
  margin: 5%;
  box-shadow: 0px -1px 13px 1px #373a3d;
  border: solid $dark 0.2rem;

  .text {
    line-height: 6rem;
    font-size: 4rem;
    padding: 3rem;
    color: $light;
    font-weight: 300;
    align-self: end;

    &Bold {
      color: $lightRed;
    }
  }

  .buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .btn {
    &SignUp,
    &Login {
      font-weight: 600;
      border: 0.2rem solid;
      border-radius: 4rem;
      margin: 1rem 5rem 1rem 5rem;
      align-self: center;
    }

    &SignUp {
      align-self: end;
      height: 9rem;
      font-size: 5rem;
      width: 40rem;
      color: $light;
      background: linear-gradient(
        180deg,
        rgb(107, 138, 240) 0%,
        rgba(0, 0, 0, 1) 100%
      );
      border-color: $dark;
    }

    &Login {
      height: 7rem;
      width: 35rem;
      border-color: $light;
      color: $light;
      background: none;
      font-size: 4rem;
    }
  }
}

.intro {
  display: grid;
  height: 100vh;
  margin-top: 8rem;
  margin-bottom: 8rem;

  .letter {
    font-size: 7rem;
    font-weight: 800;
  }
  .text {
    &First {
      padding: 0 5rem 0 5rem;
      font-size: 3.5rem;
      line-height: 7rem;
      align-self: center;
    }
    &Second {
      text-align: center;
      font-size: 3rem;
      justify-self: center;
      color: $darkBlue;
      padding: 1rem;
      margin: 5rem;
      align-self: center;
      border: 0.5rem solid $darkBlue;
    }
  }
}

.example {
  text-align: center;

  color: $darkBlue;
  padding: 10rem 0 10rem 0;
  height: 100vh;

  hr {
    padding: 0.1rem;
    background-color: $lightBlue;
    margin-bottom: 5rem;
  }

  .video {
    width: 75vw;
    height: 35rem;
  }

  .text {
    &Bold {
      font-weight: 600;
      display: block;
      font-size: 4rem;
    }
    &Italic {
      font-size: 3rem;
      margin-top: 5rem;
      display: block;
    }

    &Color {
      font-size: 2.5rem;
      display: block;
      margin-top: 2rem;
      color: $darkBlue;
      padding: 5rem;
    }
  }
}
